import {AccordionChangePayload} from '@here/hds-accordion';
import {
  VerticalNavigation,
  VerticalNavigationMode,
  VerticalNavigationSelectPayload,
} from '@here/hds-vertical-navigation';
import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {HDSAccordionProperties} from '../Accordion/HDSAccordion.js';

// eslint-disable-next-line no-new
new VerticalNavigation();

interface HDSVerticalNavigationProperties extends Omit<HDSAccordionProperties, 'onSelect'> {
  chevron?: boolean;
  navMode?: VerticalNavigationMode;
  onSelect?: (detail: VerticalNavigationSelectPayload) => void;
}

const HDSVerticalNavigation = React.forwardRef((props: HDSVerticalNavigationProperties, ref) => {
  const {onSelect, onChange, chevron, navMode, children, className, ...rest} = props;

  const innerRef = useRef<HTMLElement>(null);

  useImperativeHandle(ref, () => innerRef.current);

  const _handleSelected = (e: CustomEvent<VerticalNavigationSelectPayload>) => {
    onSelect && onSelect(e.detail);
  };

  const _handleChanged = (e: CustomEvent<AccordionChangePayload>) => {
    onChange && onChange(e.detail);
  };

  useEffect(() => {
    if (onSelect && innerRef.current) {
      innerRef.current.addEventListener('select', _handleSelected as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('select', _handleSelected as EventListener);
    };
  }, [onSelect, innerRef]);

  useEffect(() => {
    if (onChange && innerRef.current) {
      innerRef.current.addEventListener('change', _handleChanged as EventListener);
    }
    return () => {
      innerRef.current?.removeEventListener('change', _handleChanged as EventListener);
    };
  }, [onChange, innerRef]);

  return (
    <hds-vertical-navigation
      ref={innerRef}
      {...(className && {class: className})}
      {...(chevron && {chevron})}
      {...(navMode && {'nav-mode': navMode})}
      {...rest}
    >
      {children}
    </hds-vertical-navigation>
  );
});

HDSVerticalNavigation.defaultProps = {
  navMode: 'expanded',
  chevron: false,
};

export default HDSVerticalNavigation;
